import axios from 'axios'

//获取热门搜索数据
export function getHotSearch(latitude,longitude) {
    let url="static/hot-search.json";
    return new Promise( (success,error) => {
        //传递参数必须使用{}括起来
        axios.get(url)
        .then( (res) =>{
            success(res)
        }).catch( (err)=> {
            error(err)
        })
    })
}

export function getEntries(latitude,longitude){
    let url="static/entries.json";
    return new Promise((success,error)=>{
        axios.get(url)
        .then((res)=>{
            success(res)
        }).catch((err)=>{
            error(err)
        })
    })
}

export function getRestaurants(){
    let url="static/restaurants.json";
    return new Promise((success,error)=>{
        axios.get(url)
        .then((res)=>{
            success(res)
        })
        .catch((err)=>{
            error(err)
        })
    })
}


