<template>
    <div class="star":class="starType">
        <span class="star-item" :class="item" v-for="(item,index) in starClasses" :key="index">

        </span>
    </div>
</template>

<script>
//定义常量
//常量不受作用域的限制
const CLS_ON="on"         //亮的星星的数量
const CLS_OFF="off"          //不亮的星星的数量
const CLS_HALF="half"       //亮一般的星星的数量
const LENGth=5            //星星的总长度
export default {
    //接收父组件传递过来的参数:分值、尺寸
    props:{
        score:Number,
        size:Number
    },
    //根据分值计算显示几颗星星
    //computed和mehods方法一致，但是computed有缓存，methods没有缓存
    computed:{
        starType(){   //定义要使用的星星的尺寸
            return "star-"+this.size;
        },
        starClasses(){
            //判断分数是否大于0.5，如果大于则是半颗星，小于则没星
           let score= Math.floor(this.score*2)/2;
           //得到分数的整数部分
           let integer=Math.floor(score);
           //得到分数的小数部分，若该分数除以1的余数为0，则它是整数
           let hasDecimal=score%1 !== 0;
           //剩下的就是不亮的星星
           let result=[];
           for(let i=0;i<integer;i++){
               result.push(CLS_ON);  //根据整数部分判断放亮的星星的数量
           }
           if(hasDecimal){
               result.push(CLS_HALF)   //根据小数部分判断是否放半颗星星
           }
           //剩下放不亮的星星数量
           while(result.length<LENGth){     //只要你数组的长度小于你所定义的星星的长度，就让它放置不亮的星星补充长度
               result.push(CLS_OFF)
           }
           return result;
        }
    }
}
</script>
<style lang="less" scoped>
    .star{
        font-size:0;
        .star-item{
            display:inline-block;
            background-repeat: no-repeat;       
        }
        &.star-24{
            .star-item{
                width: 10px;
                height: 10px;
                margin-right: 3px;
                background-size: 10px 10px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star24_on@2x.png')
                }
                &.half{
                    background-image:url('star24_half@2x.png')
                }
                &.off{
                    background-image:url('star24_off@2x.png')
                }
            }           
        }
        &.star-36{
            .star-item{
                width: 15px;
                height: 15px;
                margin-right: 6px;
                background-size: 15px 15px;
                &:last-child{
                    margin-right: 0;
                }
                &.on{
                    background-image:url('star36_on@2x.png')
                }
                &.half{
                    background-image:url('star36_half@2x.png')
                }
                &.off{
                    background-image:url('star36_off@2x.png')
                }
            }
        }
        &.star-48{
            .star-item{
                width: 20px;
                height: 20px;
                margin-right: 9px;
                background-size: 20px 20px;
                &:last-child{
                    margin-right: 0;
                }
                background-size: 20px 20px;
                &.on{
                    background-image:url('star48_on@2x.png')
                }
                &.half{
                    background-image:url('star48_half@2x.png')
                }
                &.off{
                    background-image:url('star48_off@2x.png')
                }
            }
        }
    }
</style>





