<template>
	<footer>
	<div>
		<a @click="redirect('/msite')">
			<svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-index"></use></svg>
			<span>首页</span>
		</a>
		<a @click="redirect('/discover')">
			<svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-discoverRegular"></use></svg>
			<span>发现</span>
		</a>
		<a @click="redirect('/order')">
			<svg viewBox="0 0 22 22" id="h5-component-orderRegular" width="100%" height="100%" class="index-footerTabIcon_1EbB8wS">
			    <path fill="#818181" fill-rule="nonzero" d="M7.25 8a.75.75 0 0 1 0-1.5h7.5a.75.75 0 1 1 0 1.5h-7.5zm0 5.004a.75.75 0 1 1 0-1.5h4.498a.75.75 0 1 1 0 1.5H7.25zM3.5 3v16h9.75c2.9 0 5.25-2.35 5.25-5.25V5.748a.75.75 0 1 1 1.5 0v8.002a6.75 6.75 0 0 1-6.75 6.75H2.75a.75.75 0 0 1-.75-.75V2.25a.75.75 0 0 1 .75-.75h16.5a.75.75 0 1 1 0 1.5H3.5z">
			    	
			    </path>
		  	</svg>
