		</a>
		<a @click="redirect('/profile')">
			<svg class="index-footerTabIcon_1EbB8wS"><use xlink:href="#h5-component-profileRegular"></use></svg>
			<span>我的</span>
		</a>

	</div>

</footer>
</template>


<script>
	
	export default {
		methods:{
			redirect(router){
				this.$router.push({path:router})
			}
		}
	}


</script>
<style lang="less" scoped>
	footer{
		padding-bottom: 70px;
			div{
				height: 1.6rem;
				padding-bottom: .453333rem;
				position: fixed;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 9999;
				display: flex;
				align-items: center;
				justify-content: space-around;
				padding-top: .12rem;
				background-color: #fff;
				box-shadow: 0 -0.266667vw 0.533333vw rgba(0,0,0,.1);
				a{
					outline: none;
					color: inherit;
					text-decoration: none;
				}
				.index-footerTabIcon_1EbB8wS{
					width: .533333rem;
					height: .533333rem;
		    
				}
				span{
					display: block;
					font-size: .266667rem;
				}
			}
		
		
	}
	
</style>


